import React, {useContext} from 'react'
import { UserContext } from '../UserContext'

export const About = () => {
    const imgUrl = '../../assets/images/img_bg_1.jpg'
    const imgUrl2 = '../../assets/images/img_bg_2.jpg'

	const imgStyle = {
		backgroundImage: 'url(' + imgUrl + ')',
      };

      const imgStyle2 = {
		backgroundImage: 'url(' + imgUrl2 + ')',
	  };
	  
	  const user = useContext(UserContext);
      
    return (
           <>
           <section className="colorlib-about" data-section="about">
				<div className="colorlib-narrow-content">
					<div className="row">
						<div className="col-md-12">
							<div className="row row-bottom-padded-sm animate-box" data-animate-effect="fadeInLeft">
								<div className="col-md-12">
									<div className="about-desc" style={{textAlign: 'justify'}}>
										<span className="heading-meta">About Me</span>
										<h2 className="colorlib-heading">Who Am I?</h2>
										<p><strong>Hi I'm {`${user.nombre} ${user.apellidoPaterno}`}</strong> I am a Front End Developer and Solutions Project Leader. I have 9 years of experience in WEB development in which I have worked with AngularJS and Angular + developing SPA's (Simple Page Applications) and PWA (Progressive Web Applicatios). I have extensive domain of JavaScript and TypeScript. I have worked with tools for web development such as Bootstrap, Jquery, HightCharts, Full Calendar, Select 2 and NativeScript.</p>
										<p>I have knowledge of frameworks of reference for IT management and governance: ITIL and COBIT. I am also certified in Scrum Master</p>
										<p>I like to be constantly learning and facing challenges, I feel comfortable working under pressure and leading work teams in which I try to contribute with my experience in the analysis, design and development of web applications.</p>
									</div>
								</div>
							</div>
							<div className="row">
								<div className="col-md-3 animate-box" data-animate-effect="fadeInLeft">
									<div className="services color-4">
										<span className="icon2"><i className="icon-flag"></i></span>
										<h3>Team Leader</h3>
									</div>
								</div>
								<div className="col-md-3 animate-box" data-animate-effect="fadeInRight">
									<div className="services color-3">
										<span className="icon2"><i className="icon-globe-outline"></i></span>
										<h3>Front End</h3>
									</div>
								</div>
								<div className="col-md-3 animate-box" data-animate-effect="fadeInTop">
									<div className="services color-2">
										<span className="icon2"><i className="fab fa-angular"></i></span>
										<h3>Angular</h3>
									</div>
								</div>
								<div className="col-md-3 animate-box" data-animate-effect="fadeInBottom">
									<div className="services color-1">
										<span className="icon2"><i className="fab fa-react"></i></span>
										<h3>React</h3>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</section>
        </>
    )
}

